import type { MarkdownToHTMLRule } from '@ali/4ever-cangjie';

const validTypes = /^(warning|info|danger|success|tips)*$/

const rule: MarkdownToHTMLRule = {
  uses: [{
    plugin: () => import('markdown-it-container'),
    data: [
      'colorBlocks',
      {
        validate: function(params) {
          return params.trim().match(validTypes);
        },
        render: function (tokens, idx, _options, env, slf) {
          const m = tokens[idx].info.trim().match(validTypes);
          // add a class to the opening tag
          if (tokens[idx].nesting === 1) {
            tokens[idx].attrJoin('class', `colorBlocks ${m[1] || ''}`);
          }

          return slf.renderToken(tokens, idx, _options, env, slf);
        }
      }
    ]
  }],
};

export default rule;
